import { Post } from './post.model';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators'
import { Router } from '@angular/router';

import { environment } from '../../environments/environment';
 
const BACKEND_url = environment.apiUrl + '/posts/';


@Injectable({providedIn: 'root'}) 
// { providedIn: 'root' } is optional which is equivalent to adding Service class to providers in app.module
export class PostsService {
    private posts: Post[] = [];
    private postsUpdated = new Subject<{posts: Post[], postCount: number}>();
    constructor(private http: HttpClient, private router: Router) { }

    getPosts(postsPerPage: number, currentPage: number){
        const queryParams = `?pagesize=${postsPerPage}&page=${currentPage}`;
        this.http
            .get<{message: string; posts: any, maxPosts: number}>(BACKEND_url + queryParams)
            .pipe(map((postData) => {
                return { posts: postData.posts.map(post => {
                    return {
                        title: post.title,
                        content: post.content,
                        id: post._id,
                        imagePath: post.imagePath,
                        creator: post.creator
                    };
                }), maxPosts: postData.maxPosts};
            }))
            .subscribe(transformedPostData => {
            this.posts = transformedPostData.posts;
            this.postsUpdated.next({ 
                posts: [...this.posts], 
                postCount: transformedPostData.maxPosts});
            });

        // by adding [...] we're creating new array & restricting user from modifying the original array i.e this.posts array
    }

    getPostUpdateListener(){
        return this.postsUpdated.asObservable();
    }

    getPost(id: string) {
        return this.http.get<{
            _id: string; 
            title: string; 
            content: string;
            imagePath: string;
            creator: string
        }>(BACKEND_url + id);
    }

    addPost(title: string, content: string, image: File) {
        const postData = new FormData();
        postData.append("title", title);
        postData.append("content", content);
        postData.append("image", image, title);
        this.http.post<{message: string, post: Post}>(BACKEND_url, postData)
            .subscribe( responseData => {
                this.router.navigate(["/"]);
            });
    }

    updatePost(id: string, title: string, content: string, image: File | string) {
        let postData: Post | FormData;
        if (typeof(image) === 'object') {
            postData = new FormData();
            postData.append("id", id);
            postData.append("title", title);
            postData.append("content", content);
            postData.append("image", image, title);
        } else {
            postData = {
                id: id,
                title: title,
                content: content,
                imagePath: image,
                creator : null
            }
        }
        this.http
        .put(BACKEND_url + id, postData)
        .subscribe(response => {
            this.router.navigate(["/"]);
        });
    }

    deletePost(postId:string) {
        return this.http.delete(BACKEND_url + postId);
    }
}